.App {text-align: center;}
/* -----main----- */
.main_bg {background: url(/public/img/main.jpg);background-size: cover;width: 100vw;height: 100vh;position: absolute;top:0;left: 0;z-index: -1;}
.main_title{position: absolute;top: 40%;left: 10%;}
/* -----navber----- */
.navbar{padding: 30px 0;}
.navbar .container{width:1200px;}
.bg-light{background: transparent !important;}
.navbar-brand{margin-right: 30px;}
.nav-link{margin-right: 30px;}
/* -----common----- */
.App{background: #00000005;padding-bottom: 100px;}
.inner{width:1200px;display: flex;justify-content: space-between;align-items: center;}
._container{display: flex;justify-content: center;}
.accordion{box-shadow: 8px 8px 25px #0000002e}
/* -----info----- */
.accordion{width:100%;text-align: left;}
/* -----feed----- */
.feed .card{height: 38vh;width:19vw;flex-direction: column;align-items: center;padding: 15px;justify-content: space-between;cursor: pointer;}
.feed .card.green{background: #6faf59a1;}
.feed .card.red{background: #e51a498d;}
.feed .card.orange{background: #eba2349e;}
.feed .card:hover .img_wrap{background: rgba(255, 255, 255, 0.19);transition-duration: 0.3s;}

.feed .card .img_wrap{width: 80%;height:80%;border-radius: 50%;box-shadow: 11px 8px 20px 0px #00000012, -10px -10px 20px #ffffff26;overflow: hidden;display: flex;align-items: center;justify-content: center;transition-duration: 0.3s;}
.feed .card .img_wrap img{width: 70%;}
.feed .card .card-title{letter-spacing: -1.2px;font-size: 1.1rem;font-weight: bold;color:#000000cc;}
/* -----species----- */
.species img{height: 380px;}
.species .accordion-body{display: flex;flex-direction: column;align-items: center;}
.species .accordion-body p{width: 100%;}
/* -----disease----- */
.disease .accordion-body{display: flex;flex-direction: column;}
.disease .img_wrap{height: 20vw;text-align: center;margin-bottom: 15px;}
.disease .img_wrap img{height: 100%;}
@media all and (max-width:1200px){
    .inner{width:90%;}
    .species img{width:80%;height: auto;}
    .disease .img_wrap{height: 30vw;}
}